
import TabButton from '@/components/TabButton.vue'; // @ is an alias to /src
import {
  reactive,
  ref,
  inject,
  defineComponent,
} from 'vue';
import NoteTabModel from '@/class/NoteTabModel';
import { useStore } from 'vuex';
import { cloneDeep } from 'lodash';

export default defineComponent({
  components: {
    TabButton,
  },
  setup() {
    const store = useStore();
    const isLoading = ref(false);
    const tabList = reactive(new Array<NoteTabModel>());

    const axios: any = inject('axios');

    const loadTabList = () => {
      // retrieve all previous created tab
      axios.get('/api/tab/get-all').then((result: any) => {
        const myTabList = result.data.data;
        tabList.splice(0, tabList.length);
        myTabList.forEach((object: { tabUniqueID: string; tabNo: number; tabName: string;
          isActive: boolean; }) => {
          tabList.push(new NoteTabModel(
            object.tabNo,
            object.tabName,
            object.isActive,
            object.tabUniqueID,
          ));
          if (object.isActive) {
            store.commit('setSelectedTab', object.tabUniqueID);
          }
        });
      }).catch((err: any) => {
        console.log(err);
      });
    };

    const addNewTab = () => {
      try {
        if (store.state.isLoading === false) {
          store.commit('setLoading', true);
          const maxNo: number = tabList.reduce(
            (accumulator, currentValue) => (currentValue.tabNo > accumulator
              ? currentValue.tabNo : accumulator),
            0,
          );
          const newTab = new NoteTabModel(maxNo + 1, 'Untitled', true);

          newTab.save().then((result) => {
            store.commit('setLoading', false);

            tabList.forEach((tab, index) => {
              tabList[index].isActive = false;
            });
            tabList.push(newTab);
            store.commit('setSelectedTab', newTab.tabUniqueID);
          }).catch((err) => {
            store.commit('setLoading', false);
            alert(err);
          });
        }
      } catch (err) {
        alert(err);
      }
    };

    const selectTab = (selectedTab:NoteTabModel) => {
      if (store.state.isLoading === false) {
        store.commit('setLoading', true);
        axios.post(`/api/tab/select/${selectedTab.tabUniqueID}`).then((result: any) => {
          store.commit('setLoading', false);
          const myResponse = result.data;

          if (myResponse.statusCode === 200) {
            store.commit('setSelectedTab', selectedTab.tabUniqueID);
            // eslint-disable-next-line no-param-reassign
            tabList.forEach((tab) => { tab.isActive = false; });
            // eslint-disable-next-line no-param-reassign
            selectedTab.isActive = true;
          } else {
            alert(myResponse.message);
          }
        }).catch((err: any) => {
          store.commit('setLoading', false);
          alert(err);
        });
      }
    };

    const swapTab = (selectedTab:NoteTabModel, direction: string) => {
      if (direction === 'left') {
        const currentIndex = tabList.indexOf(selectedTab);

        if (currentIndex - 1 >= 0) {
          if (store.state.isLoading === false) {
            store.commit('setLoading', true);
            // swap
            const targetTabToSwap: NoteTabModel = cloneDeep(tabList[currentIndex - 1]);
            const myTab: NoteTabModel = cloneDeep(selectedTab);
            const tempTabNo = targetTabToSwap.tabNo;
            targetTabToSwap.tabNo = selectedTab.tabNo;
            myTab.tabNo = tempTabNo;
            Promise.all([myTab.save(), targetTabToSwap.save()]).then((result) => {
              store.commit('setLoading', false);

              tabList[currentIndex] = targetTabToSwap;
              tabList[currentIndex - 1] = myTab;
            }).catch((err) => {
              store.commit('setLoading', false);
              alert(err);
            });
          }
        }
      } else {
        const currentIndex = tabList.indexOf(selectedTab);

        if (currentIndex + 1 < tabList.length) {
          if (store.state.isLoading === false) {
            store.commit('setLoading', true);
            // swap
            const targetTabToSwap = tabList[currentIndex + 1];
            const myTab = cloneDeep(selectedTab);
            const tempTabNo = cloneDeep(targetTabToSwap.tabNo);
            targetTabToSwap.tabNo = selectedTab.tabNo;
            myTab.tabNo = tempTabNo;
            Promise.all([myTab.save(), targetTabToSwap.save()]).then((result) => {
              store.commit('setLoading', false);

              tabList[currentIndex] = targetTabToSwap;
              tabList[currentIndex + 1] = myTab;
            }).catch((err) => {
              store.commit('setLoading', false);
              alert(err);
            });
          }
        }
      }
    };

    const removeNote = (tab: NoteTabModel) => {
      if (store.state.isLoading === false) {
        store.commit('setLoading', true);
        tab.delete().then((result) => {
          store.commit('setLoading', false);
          if (result === 'OK') {
            const foundIndex = tabList.indexOf(tab);

            tabList.splice(foundIndex, 1);
            selectTab(tabList[foundIndex - 1 >= 0 ? foundIndex - 1 : 0]);
          }
        }).catch((err) => {
          store.commit('setLoading', false);
          alert(err);
        });
      }
    };

    const updateTab = (tab:NoteTabModel) => {
      if (store.state.isLoading === false) {
        store.commit('setLoading', true);
        tab.save().then((result) => {
          store.commit('setLoading', false);
        }).catch((err) => {
          store.commit('setLoading', false);
          alert(err);
        });
      }
    };

    // intiialize function
    loadTabList();

    return {
      isLoading,
      tabList,
      addNewTab,
      removeNote,
      selectTab,
      updateTab,
      swapTab,
    };
  },
});
